.App {
	text-align: center;
	min-height: 100vh;
	background-color: #f8f7ff;
}

.courses {
	height: 600px;
	width: 500px;
	border-radius: 20px;
	margin: 20px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	box-shadow: 0px 5px 20px #d8dcff;
	background: white;
}

.PaperLeft {
	margin-top: 20px;
	padding: 20px;
	margin-bottom: 10px;
	display: flex;
}

.PaperRight {
	margin-top: 20px;
	padding: 20px;
	margin-bottom: 10px;
}

.dropdown {
	width: 400px;
	padding: 20px;
	margin:20px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-end;
}

.ListedCourses {
	padding-left: 20px;
	margin-left: 20px;
	
}
.TabsAppBar.TabsAppBar {
	background-color: #aeadf0;
	color: black;
}
.TabsBar.TabsBar {
	font-size: 18px;
	font-weight: bolder;
}

.TabsLayoutPanel {
	background-color: #f8f7ff;
}

.altcoursesDropdown, .CoursesDropdown {
	background-color: white;
}

h3, h4 {
	margin-top: 0;
}

.TabsRoot {
	flex-grow: 1;
}
.CreditHistoryTable {
	min-width: 400px;
}


@media screen and (min-width: 960px) {
	.courses {
		height: 350px;
		width: 600px;
		border-radius: 20px;
		margin: 20px;
		padding: 10px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		box-shadow: 0px 5px 20px #d8dcff;
		background: white;
	}
}

